.App {
  font-family: sans-serif;
  text-align: center;
  background-color: lightblue;
  border-radius: 7px;
}
input {
  display: block;
  margin-left: auto;
  margin-right: auto;
  padding: 5px;
  width: 20%;
  height: 20px;
  border-radius: 5px;
  outline: none;
  border-radius: 3-x solid steelblue;
}
.card-body {
  border: 1px solid gray;
  padding: 10px;
  justify-content: center;
  width: 50%;
  border-radius: 7px;
  text-align: left;
  margin-left: auto;
  margin-right: auto;
  margin-top: 20px;
  background-color: steelblue;
  box-shadow: 5px 10px 15px gray;
}
h1 {
  padding-top: 15px;
  font-size: 40px;
}
